import React from "react";
import styles from "./variable.module.css";
import TimeGraph from "./TimeGraph";
import { Redirect } from "react-router-dom";
function Variable(props) {
  console.log(props.variable);
  const dailyinfo = [];
  for (let i = 0; i < 24; i++) {
    dailyinfo.push({ catches: 0, durationFished: 0 });
  }

  const yearlyInfo = [];
  for (let i = 0; i < 372; i++) {
    yearlyInfo.push({ catches: 0, durationFished: 0 });
  }
  const variable = props.variable.lakes
    ? props.variable.lakes[0]
    : props.variable;

  if (variable.castIndexes === undefined) {
    return <Redirect to="/fishv3/manage"> </Redirect>;
  }
  variable.castIndexes.map((castIndex, index) => {
    let reelInTime = props.mSToDate(props.castHistory[castIndex].reelInTime);
    let castDuration = props.castHistory[castIndex].duration;

    let reelInHour = reelInTime.getHours();
    let reelInMinute = reelInTime.getMinutes();

    if (props.castHistory[castIndex].catch) {
      dailyinfo[reelInHour].catches += 1;
    }
    let reelInHourMins = reelInMinute;
    dailyinfo[reelInHour].durationFished += reelInHourMins;
    for (let hour = 1, minutes = castDuration / 60000; minutes > 0; hour++) {
      console.log(minutes);
      if (minutes > 59) {
        minutes -= 60;
        dailyinfo[(reelInHour + hour) % 24].durationFished += 60;
      } else {
        dailyinfo[(reelInHour + hour) % 24].durationFished += minutes;
        minutes -= minutes;
      }
    }
  });

  return (
    <div className={styles.container}>
      <div className={styles.variableHeader}>
        <div className={styles.navButton}>&#x2190;</div>
        <div className={styles.variableName}>{variable.name}</div>
        <div className={styles.navButton}>&#x2192;</div>
      </div>
      <div className={styles.basicVarInfo}>
        <div className={styles.spaceHolderOne}></div>
        <div className={styles.basicInfo}>
          <div>Catches: {variable.catches}</div>
          <div>Time cast: {props.mSToReadable(variable.duration)}</div>
          <div>Casts: {variable.castIndexes.length}</div>
          <div></div>
        </div>
      </div>
      <div className={styles.timeInfo}>
        <div>
          <div className={styles.dailyTime}>
            <div className={styles.dailyYScale}>
              <div>0h</div>
              <div>5h</div>
              <div>10h</div>
              <div>15h</div>
              <div>20h+</div>
            </div>
            <TimeGraph times={dailyinfo}></TimeGraph>
          </div>
          <div className={styles.dailyXScale}>
            {dailyinfo.map((time, index) => (
              <div style={{ width: `${90 / 24}vw`, textAlign: "center" }}>
                {index}
              </div>
            ))}
          </div>
        </div>
        <div className={styles.yearlyTime}>
          <TimeGraph times={yearlyInfo}></TimeGraph>
        </div>
      </div>
    </div>
  );
}

export default Variable;
